html {
  height: 100vh;
  /*overflow: hidden;*/
}

body { 
  margin:0;
  padding:0;
  -webkit-perspective: 1px;
          perspective: 1px;
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
  height: 100%;
  overflow-y: scroll;
  overflow-x: hidden;
  font-family: Nunito;
  -webkit-transition: all 1s ease-in-out;
  transition: all 1s ease-in-out;
}

